@import url("https://fonts.googleapis.com/css2?family=Jost&display=swap");
body{
    margin: 0;
    padding: 0;
}
.forum-container{
    display: flex;
    flex-direction: row;
    justify-content: space-between;
}
.create-thread{
    background-color: rgb(23, 193, 23);
    border-radius: 5px;
    border: 1px solid black;
    font-family: "Jost";
    font-size: 20px;
    margin: 20px;
    cursor: pointer;
}
.search-bar{
    border-radius: 5px;
    border: 1px solid black;
    height: 25px;
    width: 300px;
    margin: 20px;
}
.contents-container h2{
    text-align: center;
}
.contents-container{
    text-align: center;
}
.forum-form{
    text-align: center;
    position: absolute;
    top:50%;
    left: 50%;
    transform: translate(-50%, -50%);
    display: flex;
    flex-direction: column;
    background-color:rgb(69, 66, 66);
    padding: 50px;
    border: 1px solid black;
    display: none;
}

.item{
    margin: 10px;
    width: 250px;
    height: 22px;
}
.recent-item{
    background-color: rgb(59, 59, 59);
}
.cat-title{
    background-color: rgb(67, 64, 64);
    font-size: 20px;
    padding: 0;
    margin: 0;
    border-radius:200px ;
}

.thread{
    text-align: start;
    padding-left:10px;
}
.cat{
    display: flex;
    flex-direction: column;
    background-color: rgb(58, 58, 58);
}
.cat h5{
    padding: 0;
    margin: 0;
}
.comments-number{
    position: relative;
    bottom:0;
}
.container-post{
    display: flex;
}
.thread, .comments-number{
    width: 50%;
    overflow: hidden;
}
.comments-number{
    text-align: end;
    color:white
}

